import { createElement, useRef, useEffect, useState } from 'rax';
import View from 'rax-view';
import './index.css';
import Player from '../player/player';
import Loader from '../player/format/hls/loader';
import * as Chart from 'echarts';
import { getPieOption } from './webUI';
let chart;
var Tagnames;
(function (Tagnames) {
    Tagnames["decodeAvgTime"] = "\u6BCF\u5E27\u89E3\u7801\u5E73\u5747\u8017\u65F6";
    Tagnames["decodeCount"] = "\u5DF2\u89E3\u7801\u5E27\u6570";
    Tagnames["fps"] = "fps";
    Tagnames["renderAvgErrorTime"] = "\u5E73\u5747\u6E32\u67D3\u8BEF\u5DEE";
    Tagnames["renderCount"] = "\u6E32\u67D3\u6B21\u6570";
    Tagnames["dropFrames"] = "\u4E22\u5E27\u6B21\u6570";
    Tagnames["duration"] = "\u89C6\u9891\u957F\u5EA6";
    Tagnames["startTime"] = "\u8D77\u59CB\u64AD\u653E\u65F6\u95F4";
    Tagnames["currentTime"] = "\u5F53\u524D\u64AD\u653E\u65F6\u95F4";
    Tagnames["decodeSumTime"] = "\u89E3\u7801\u603B\u8017\u65F6";
    Tagnames["frameLength"] = "\u89E3\u7801\u7F13\u51B2\u533A\u672A\u6D88\u8D39\u5E27\u6570";
    Tagnames["frameInfo"] = "\u5E27\u5206\u8FA8\u7387";
    Tagnames["bufferedTime"] = "\u7F13\u51B2\u533A\u65F6\u95F4";
})(Tagnames || (Tagnames = {}));
;
var Scale;
(function (Scale) {
    Scale["decodeAvgTime"] = "ms";
    Scale["renderAvgErrorTime"] = "ms";
    Scale["duration"] = "s";
    Scale["startTime"] = "s";
    Scale["currentTime"] = "s";
    Scale["decodeSumTime"] = "ms";
    Scale["bufferedTime"] = "s";
})(Scale || (Scale = {}));
const renderItem = (key, value) => {
    return <div className='tag' key={key}>
      <div className='tag_key'>{key + ': '}</div>
      <div className='tag_value'>{value}</div>
    </div>;
};
export const renderItems = list => {
    return Object.entries(list).map(([key, value]) => {
        if (Scale[key]) {
            if (!isNaN(Number(value))) {
                value = Number(value).toFixed(4);
            }
            value = value.toString() + Scale[key];
        }
        if (Tagnames[key]) {
            return renderItem(Tagnames[key], value);
        }
        return null;
    });
};
const MyComponent = () => {
    const cav = useRef(null);
    const div = useRef(null);
    const [state, setState] = useState({
        performanceStatus: null,
        metadata: null,
        currentTime: null,
        frameInfo: null,
        isplaying: false,
        bufferedTime: null
    });
    const { performanceStatus, metadata, currentTime, frameInfo, isplaying, bufferedTime } = state;
    const handlePlay = () => {
        window.player.play();
    };
    const handlePause = () => {
        window.player.pause();
    };
    const getPerformanceStatus = () => {
        const status = window.player.performanceStatus;
        setState(pstate => (Object.assign(Object.assign({}, pstate), { performanceStatus: status })));
        window.player.getBufferSize();
    };
    const handleCanplay = () => {
        getPerformanceStatus();
        setInterval(() => {
            getPerformanceStatus();
        }, 1000);
    };
    const renderPie = payload => {
        const data = [{
                value: payload.bf,
                name: '原始流'
            }, {
                value: payload.packet,
                name: 'packet'
            }, {
                value: payload.frame,
                name: 'frame'
            }];
        chart.setOption(getPieOption(data));
    };
    const handleLoadMeta = payload => {
        setState(pstate => (Object.assign(Object.assign({}, pstate), { metadata: payload })));
    };
    const handlePlayerTimeUpdate = () => {
        const buffered = window.player.buffered;
        const bufferedTime = buffered.end - buffered.start;
        setState(pstate => (Object.assign(Object.assign({}, pstate), { bufferedTime: bufferedTime, currentTime: window.player.currentTime })));
    };
    const handleLoadFrameInfo = payload => {
        setState(pstate => (Object.assign(Object.assign({}, pstate), { frameInfo: payload })));
    };
    const handlePlayStatusChange = status => {
        setState(pstate => (Object.assign(Object.assign({}, pstate), { isplaying: status })));
    };
    useEffect(() => {
        chart = Chart.init(div.current, 'dark');
        if (cav.current) {
            const player = new Player('http://127.0.0.1:8083/playlist.m3u8', {
                canvas: cav.current,
                baseTime: Loader.baseTime // loader: Loader,
            });
            window.player = player;
            player.on('canplay', () => handleCanplay()); // player.on('progress', () => console.log('progress'));
            player.on('playing', () => handlePlayStatusChange(true));
            player.on('pause', () => handlePlayStatusChange(false)); // recieveWorkerBufferSize
            player.on('recieveWorkerBufferSize', payload => renderPie(payload));
            player.on('loadedmetadata', payload => handleLoadMeta(payload));
            player.on('timeupdate', () => handlePlayerTimeUpdate());
            player.on('loadFrameInfo', payload => handleLoadFrameInfo(payload));
            console.log(player);
        }
    }, []);
    return <View className="container">
      <canvas ref={cav} className='cav'/>
      <div className='controlWrap'>
        <div className='btn' onClick={handlePlay}>play</div>
        <div className='btn' onClick={handlePause}>pause</div>
      </div>
      <div className='infoWrap'>
        <div ref={div} className='pieWrap'></div>
        {performanceStatus && <div className='tagWrap'>
          <div className='tagTitle'>性能参数</div>
          {renderItems(performanceStatus)}
        </div>}
        {metadata && <div className='metaWrap'>
          <div className='metaTitle'>视频参数</div>
          {renderItems(metadata)}
          {frameInfo && renderItems({
                'frameInfo': `${frameInfo.width} x ${frameInfo.height}`
            })}
          {renderItems({
                currentTime
            })}
          {renderItems({
                bufferedTime
            })}
          <div className='status_wrap'>
            <div className='status_key'>{`当前播放状态: `}</div>
            <div className='status_value'>{isplaying ? '播放中' : '暂停'}</div>
          </div>
        </div>}
      </div>
    </View>;
};
export default MyComponent;
